// 
export default {
	/**
	 * HTTP
	 * @param String url 
	 * @param Object data 
	 * @param Function success 
	 * @param String method 
	 * @param Boolean block 
	 */
	request(url, data = {}, success, method = 'POST', block = false) {
		// 
		if (block) {
			if (this.isBlock) {
				this.toast("")
				return
			}
			this.isBlock = true
		}

		data.token = this.token || '' // token
		data.platform = this.platform // 
		// token
		// data.token = 'Y1J3S2l2OGw1eVhCcjNMVmRwWHNNVFUzTkRrNU1qUTBNU1l4TVRNdU1qSXlMakkzTGpJeE1DWXlPVFl5TWc9PQ=='
		uni.request({
			url: ~url.indexOf('http') ? url : this.HTTP_URL + url,
			method,
			data,
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: res => {
				this.isBlock = false
				if (res.statusCode != 200) {
					// 
					this.toast('')
				} else if (res.data.code == 200 || res.data.code == 201) {
					// 
					this.login(() => {
						this.request(url, data, success, method, block)
					})
				} else if (res.data.code != 0) {
					// 
					this.toast(res.data.msg)
				} else {
					// 
					success && success(res.data)
				}
			},
			fail: res => {
				console.error('' + JSON.stringify(res))
			}
		});
	},
	/**
	 * 
	 * @param String url 
	 */
	goPage(url) {







		uni.navigateTo({
			url,
			fail() {
				uni.redirectTo({
					url,
					fail() {
						uni.switchTab({
							url,
							fail() {
								uni.reLaunch({
									url
								})
							}
						})
					}
				})
			}
		})
	},

	toast(content, icon = 'none', duration = 1500) {
		uni.showToast({
			title: content,
			icon,
			duration
		})
	},
	modal(content, callBack = false, confirmText = '') {
		uni.showModal({
			title: '',
			content,
			showCancel: Boolean(callBack),
			confirmText,
			success: res => {
				res.confirm && callBack && callBack()
			},
		});
	},
	// 
	copy(data) {
		uni.setClipboardData({
			data: data.toString(),
			success: () => this.toast('', 'success')
		});
	},
	/** */
	getEle(dom, callBack) {
		//
		var query = uni.createSelectorQuery();
		//id
		query.select(dom).boundingClientRect()
		query.exec(callBack)
	},
	// 
	preImg(img) {
		let imgPath = img
		if (typeof(img) == 'string') {
			imgPath = [img]
		}
		uni.previewImage({
			urls: imgPath
		});
	},
	// 
	setData(key, value) {
		this[key] = value

		uni.setStorage({
			key,
			data: value
		})
	},
	// 
	getData(key) {
		let value = this[key]

		if (value === undefined) {
			value = uni.getStorageSync(key)
			this[key] = value
		}

		return value
	},
	
	setVal(key, value) {
		this[key] = value
	},
	getVal(key) {
		return this[key]
	},

	/**
	 * 
	 */
	getPage() {
		return getCurrentPages()[getCurrentPages().length - 1] && getCurrentPages()[getCurrentPages().length - 1].$vm || null
	},

	/**
	 * 
	 * @param Array file 
	 */
	upload(file, callBack) {
		uni.showLoading({
			title: '...'
		})
		if (typeof file == 'string') {
			file = [file]
		}
		let realFile = []
		for (let i = 0; i < file.length; i++) {
			if (!file[i]) {
				file.splice(i, 1)
			} else {
				uni.uploadFile({
					url: this.HTTP_URL + 'upload',
					filePath: file[i],
					name: 'file',
					success: res => {
						res = JSON.parse(res.data)

						realFile[i] = res.data.url
						if (realFile.length == file.length) {
							//  
							uni.hideLoading()
							callBack && callBack(realFile)
						}
					}
				});
			}
		}

	},
	// 
	openVideoAd(success, kindness = true) {
		const page = this.getPage()
		let adUnitId
		if (this.platform == 'MP-WEIXIN') {
			adUnitId = this.adUnitId
		} else if (this.platform == 'MP-QQ') {
			adUnitId = this.qq_adUnitId
		}
		// 
		if (!page.videoAd) {
			uni.showLoading({
				title: "..."
			})
			if (wx.createRewardedVideoAd) {
				page.videoAd = wx.createRewardedVideoAd({
					adUnitId
				})
			} else {
				this.modal('')
			}

			page.videoAd.onClose(status => {
				if (status && status.isEnded || status === undefined) {
					// 
					success && success()
				} else {
					this.toast('')
				}
			})

			page.videoAd.onError(err => {
				this.toast("")
				kindness && success && success()
			})
			uni.hideLoading()
		}
		// 
		page.videoAd.show().catch(err => {
			// 
			page.videoAd.load().then(() => {
				page.videoAd.show().catch(err => {
					this.toast('')
				})
			})
		})
	},

	/***/
	openInterstitialAd() {
		const page = this.getPage()
		if (!page.interstitialAd) {
			page.interstitialAd = wx.createInterstitialAd({
				adUnitId: this.interstitialAd_adUnitId
			})
		}

		if (page.interstitialAd) {
			page.interstitialAd.show().catch(err => {
				console.log(err)
			})
		} else {
			console.error('')
		}

	}

}
