// 
export default {
	/** */
	login(callBack) {
		const getAuth = function(code) {
			// 
			this.request(this.API.AUTH_LOGIN, {
				model: this.getData('sysInfo')['model'],
				code,
				js_code: code, // 
			}, res => {
				if (res.data.token) this.token = res.data.token
				callBack && callBack()
			})
		}.bind(this)


		uni.login({
			success: res => {
				getAuth(res.code)
			},
		});















	},
	/** */
	checkUpdate() {

		const updateManager = uni.getUpdateManager();
		updateManager.onUpdateReady(function() {
			//  applyUpdate 
			updateManager.applyUpdate();
		});

	},

	/***/
	formatNumber(num, fix = 1) {
		if (num >= 10000) {
			if(num % 10000==0) fix = 0;
			return (num / 10000).toFixed(fix) + ''
		} else {
			return num
		}
	},

	// 
	invokeSocket() {
		if (!this.socketTask || this.socketTask.readyState == 2 || this.socketTask.readyState == 3) {
			this.socketTask = uni.connectSocket({
				url: this.WSS_URL,
				success: res => console.log('WebSocket ', res)
			})

			this.socketTask.onMessage(res => {
				const msg = JSON.parse(res.data)
				console.log(' WebSocket', msg);
				const {
					type,
					data
				} = msg
				const page = this.getPage() // 

				switch (type) {
					case 'init': // ClientId user_id
						this.setData('clientId', data)
						// this.getData('userInfo') && this.request(this.API.USER_BIND, {
						// 	client_id: data
						// })
						// clientIdjoinGroup
						page && page.$refs.guildComponent &&
							page.$refs.guildComponent.joinGroup && page.$refs.guildComponent.joinGroup()
						break;
					case 'chartMsg': // 
						page && page.$refs.guildComponent &&
							page.$refs.guildComponent.addChartMsg && page.$refs.guildComponent.addChartMsg(data)
						break;
					case 'sayworld': // 
						this.danmakuQueue.push(data)
						break;
					case 'sendHot': // 
						page && page.$refs.guildComponent &&
							page.$refs.guildComponent.sendHotDanmaku && page.$refs.guildComponent.sendHotDanmaku(data)
						break;
					case 'sendItem': // 
						page && page.$refs.guildComponent &&
							page.$refs.guildComponent.itemEffect && page.$refs.guildComponent.itemEffect(data)
						break;
					default:
						break;
				}
			})

			this.socketTask.onClose(res => {
				console.log('WebSocket ', res);
				if (res.code == 1006) {
					// 
					// "abnormal closure"
					this.invokeSocket()
				}
				// reason : "interrupted" // "abnormal closure"
			})
		}
	},
	/**
	 * loading
	 */
	closeLoading(page) {
		if (--page.requestCount <= 0) {
			page.$nextTick(function() {
				uni.stopPullDownRefresh()
				page.showLoading = false
			})
		}
	},
	/***/
	commonShareAppMessage(shareType) {
		//  shareTicket 
		uni.showShareMenu({
			withShareTicket: true
		});

		// -----
		// 
		// referrer
		let path = `/pages/index/index?referrer=${this.getData('userInfo').id}`
		if (this.getData('userStar').id) {
			// 
			path += `&starid=${this.getData('userStar').id}`
		}
		// 
		let title = this.getData('config').share_text
		// 
		let imageUrl = this.getData('userStar').share_img || this.getData('config').share_img

		// ----- 
		if (shareType) {
			const share = this.getData('config').share_cfg[shareType - 1]
			if (share) {
				if (share.title) title = share.title
				if (share.path) path += `&${share.path}`
				if (share.imageUrl) imageUrl = share.imageUrl
			}

		}
		//--------------
		title = title.replace(/STARNAME/g, this.getData('userStar').name || '')
		title = title.replace(/MYNAME/g, this.getData('userInfo').nickname || this.NICKNAME)
		console.log(path);
		return {
			title,
			path,
			imageUrl,
		}
	},

	/**
	 * 
	 * @param {Object} src
	 * @param {Object} callback
	 */
	getLocalImg(src, callback) {
		if (~src.indexOf('http')) {
			// 
			uni.getImageInfo({
				src,
				success: res => {
					callback && callback(res.path)
				},
				fail: res => {
					console.error(res)
				}
			})
		} else {
			// 
			callback && callback(src)
		}
	},

	/**
	 * canvas
	 * @param {String} canvasId  canvas  canvas-id 
	 * @param {Object} data 
	 * @param {Function} callbcak 
	 */
	getCanvasImg(canvasId, data, callback) {
		uni.showLoading({
			title: ""
		})
		let ctx = uni.createCanvasContext(canvasId, this);
		let rate = this.getData('sysInfo').windowWidth / 375 / 2
		// ------
		// 
		this.getLocalImg(data.img.bg, src => {
			ctx.drawImage(src, 0, 0, 480 * rate, 854 * rate);

			//  
			this.getLocalImg(data.img.star, src => {
				ctx.drawImage(src, 48 * rate, 286 * rate, 382 * rate, 305 * rate);

				// 
				this.getLocalImg(data.img.avatar, src => {
					ctx.save() //
					ctx.beginPath() //
					ctx.arc(79 * rate, 784 * rate, 40 * rate, 0, 2 * Math.PI, false) //
					ctx.clip() //
					ctx.drawImage(src, 38 * rate, 744 * rate, 80 * rate, 80 * rate) //
					ctx.restore() //

					// 
					this.getLocalImg(data.img.qrcode, src => {
						ctx.save() //
						ctx.beginPath() //
						ctx.arc(400 * rate, 780 * rate, 50 * rate, 0, 2 * Math.PI, false) //
						ctx.clip() //
						ctx.drawImage(src, 350 * rate, 730 * rate, 100 * rate, 100 * rate);
						ctx.restore() //

						// ------
						ctx.setFillStyle('#FFFFFF') //

						ctx.setFontSize(18) //10
						ctx.setTextAlign('center')
						data.text.title[0] && ctx.fillText(data.text.title[0], 240 * rate, 200 * rate) //
						data.text.title[1] && ctx.fillText(data.text.title[1], 240 * rate, 250 * rate) //

						ctx.fillText(data.text.starname, 140 * rate, 632 * rate) //

						ctx.setFontSize(10) //10
						ctx.setTextAlign('left')
						ctx.fillText(`:NO.${data.text.weekRank}`, 270 * rate, 616 * rate) //
						ctx.fillText(`:${data.text.weekHot}`, 270 * rate, 640 * rate) //

						ctx.fillText(`${data.text.myname}`, 130 * rate, 774 * rate) //
						ctx.fillText(`${data.text.starname}`, 130 * rate, 804 * rate) //

						// ------
						ctx.draw(false, () => {
							uni.canvasToTempFilePath({
								canvasId,
								success: res => {
									uni.hideLoading()
									callback && callback(res.tempFilePath)
								}
							})
						})
					})
				})
			})
		})
	},

}
